@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
*{margin:0;padding:0;border:0;box-sizing:border-box;font-family:"Montserrat",sans-serif}#root{position:relative;min-height:100vh;display:flex;flex-direction:column}a{text-decoration:none}
.container-centre-screen{display:flex;justify-content:center;align-items:center;min-height:100vh}
.modal{position:fixed;bottom:0;z-index:1;background-color:#534f13;color:#fff;font-weight:bold;font-size:.9em;width:100%;padding:10px;transition:all .8s}.btn-container{margin:5px 0px}.accept-cookie{padding:4px;font-weight:bold;border-radius:4px}
